@import "./Variables.scss";

@mixin flexColumnWrap {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
}
@mixin flexColumnCenter {
  display: flex;
  flex-direction: column;
  align-items: center;
}

@mixin flexRowCenter {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@mixin buttonStyle {
  border: none;
  color: $White;
  background-color: $black;
  width: 150px;
  height: 40px;
  font-weight: bold;
  font-size: 0.9rem;
  letter-spacing: 1px;
  margin: 1rem 1rem;
  border-radius: 5px;
  letter-spacing: 4px;
  transition: 0.4s;
}

@mixin listStyle {
  list-style: none;
  text-decoration: none;
}
